<template>
  <div class="loading">
    <img src="../assets/img/xhr.jpeg" alt="">
  </div>
</template>
<script>
  export default {
    name: 'loading',
    data () {
      return {
        activeTab: 'tab1'
      }
    },
    methods: {
      handleTabChange (val) {
        this.activeTab = val
      }
    },
    mounted () {
      setTimeout(() => {
        this.$router.push({name: 'about_me'})
      }, 2000)
    }
  }
</script>
<style lang="">
	@keyframes index{
		0{
			width: 100%;
			opacity: 1;
			transform: rotate(0);
		}
		100%{
			width: 0;
			opacity: 0;
			transform: rotate(720deg);
		}
	}
  .loading{
		width: 100vw;
		height: 100vh;
		display: -webkit-flex;
		display: flex;
		justify-content: center;
		align-items: center;
	}
  img{
			width: 30%;
			animation: index 2s ease-out 1s;
		}
</style>



