<template>
<mu-paper>
  <mu-bottom-nav :value="bottomNav" @change="handleChange">
    <mu-bottom-nav-item to='/about_me' value="about me" title="me" icon="face"/>
    <mu-bottom-nav-item to='/skills' value="favorites" title="skills" icon="star"/>
    <mu-bottom-nav-item to='/project' value="nearby" title="project" icon="folder"/>
  </mu-bottom-nav>
</mu-paper>
</template>
<script>
export default {
  name: 'list',
  data () {
    return {
      bottomNav: 'about me'
    }
  },
  methods: {
    handleChange (val) {
      this.bottomNav = val
    }
  }
}
</script>
<style lang="">
  .mu-bottom-nav{
    position: absolute;
    bottom: 0;
    z-index: 99999;
    background-color: #eee;
  }
  .tab-color{
    background-color: #007ACC;
  }
  .mu-tab-link-highlight{
    background-color: #000
  }

</style>



