<template>
<div class="skills">
  <mu-raised-button class="box"  id="top" label="技术栈" @click="open('left')"/>
  <mu-raised-button class="box"  id="bottom" label="自我评价" @click="open('bottom')"/>
  <mu-raised-button class="box"  id="left" label="工作经历" @click="open('right')"/>
  <mu-raised-button class="box"  id="right" label="PDF简历" @click="open('top')"/>
  <mu-popup position="bottom" popupClass="demo-popup-bottom" :open="bottomPopup" @close="close('bottom')">
    <mu-appbar title="keep dreaming" style="text-align:center;">
    </mu-appbar>
    <mu-content-block>
      <h2>我是一个性格幽默、做事认真的人。</h2>
      <h2>我喜欢研究所碰到的各种技术,并且非常享受这个过程。</h2>
      <h2>有较强的自主编程能力及团队合作，团队交流能力。</h2>
      <h2>对工作充满热情, 吃苦耐劳，能在较大的压力下保持良好的工作状态。</h2>
    </mu-content-block>
  </mu-popup>
  <mu-popup position="top" popupClass="demo-popup-top" :open="topPopup">
       <a href="./gq.pdf" target="_blank">PDF简历下载</a>
  </mu-popup>
  <mu-popup position="left" popupClass="demo-popup-left" :open="leftPopup" @close="close('left')">
    <div>
      <mu-raised-button label="HTML"  primary fullWidth/>
      <mu-raised-button label="CSS3" primary fullWidth/>
      <mu-raised-button label="js" primary fullWidth/>
      <mu-raised-button label="ES6" primary fullWidth/>
      <mu-raised-button label="vue" primary fullWidth/>
      <mu-raised-button label="jquery" primary fullWidth/>
      <mu-raised-button label="node" primary fullWidth/>
      <mu-raised-button label="git" primary fullWidth/>
      <mu-raised-button label="svn" primary fullWidth/>
      <mu-raised-button label="小程序" primary fullWidth/>
      <mu-raised-button label="微信公众号" primary fullWidth/>
    </div>  
  </mu-popup>
  <mu-popup position="right" popupClass="demo-popup-right" :open="rightPopup" @close="close('right')">
    <div>
      <mu-raised-button label="2015.08 ~2016.12"  primary fullWidth/>
      <mu-raised-button label="烟台佳创网络科技有限公司"  primary fullWidth/>
      <mu-raised-button label="前端开发工程师"  primary fullWidth/>
      <mu-raised-button label="主要使用div+css+jq"  primary fullWidth/>
      <mu-raised-button label="主要负责网站前端界面实现"  primary fullWidth/>
      <mu-raised-button label="2017.03 ~至今"  primary fullWidth/>
      <mu-raised-button label="苏州益发特科技有限公司"  primary fullWidth/>
      <mu-raised-button label="前端开发工程师"  primary fullWidth/>
      <mu-raised-button label="主要使用vue进行项目开发"  primary fullWidth/>
      <mu-raised-button label="配合后端人员实现数据交互"  primary fullWidth/>
    </div>
  </mu-popup>
</div>
</template>

<script>
export default {
  data () {
    return {
      bottomPopup: false,
      topPopup: false,
      leftPopup: false,
      rightPopup: false,
      screenWidth: document.body.clientWidth,
      timer: false,
      ds: false
    }
  },
  methods: {
    open (position) {
      this[position + 'Popup'] = true
    },
    close (position) {
      this[position + 'Popup'] = false
    },
    del () {
      document.getElementById('top').style.top = 0
      document.getElementById('right').style.right = 0
      document.getElementById('right').style.top = 0
      document.getElementById('left').style.top = 0
      document.getElementById('bottom').style.top = 0
    },
    init () {
      document.getElementById('top').style.top = '42%'
      document.getElementById('right').style.right = '20%'
      document.getElementById('right').style.top = '25%'
      document.getElementById('left').style.top = '42%'
      document.getElementById('bottom').style.top = '60%'
    }
  },
  watch: {
    topPopup (val) {
      if (val) {
        setTimeout(() => {
          this.topPopup = false
        }, 2000)
      }
    },
    screenWidth (val) {
      if (!this.timer) {
        this.timer = true
        const that = this
        setTimeout(() => {
          if (val <= '866') {
            that.del()
          } else {
            that.init()
          }
          that.timer = false
        }, 400)
      }
    }
  },
  mounted () {
    if (document.body.clientWidth <= '866') {
      document.getElementById('top').style.top = 0
      document.getElementById('right').style.right = 0
      document.getElementById('right').style.top = 0
      document.getElementById('left').style.top = 0
      document.getElementById('bottom').style.top = 0
    }
    // 获取当前屏幕宽度
    const that = this
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        that.screenWidth = window.screenWidth
      })()
    }
  }
}
</script>

<style lang="css">
.skills{
  width: 100%;
  height: 100%;
  color: #f3ebeb !important;
  text-align: center;
  background: url(../assets/img/bg2.jpg)no-repeat;
  background-size: 100% 100%;
}
.demo-popup-top {
  width: 100%;
  opacity: .8;
  height: 48px;
  line-height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 375px;
}

.demo-popup-left {
  display: flex;
  width: 85%;
  max-width: 300px;
  height: 100%;
  align-items: center;
  padding: 24px;
}

.demo-popup-right {
  display: flex;
  width: 100%;
  height: 100%;
  max-width: 300px;
  align-items: center;
  padding: 24px;
}

.demo-popup-bottom {
  width: 100%;
  max-width: 375px;
}
.box{
  width: 10vw;
  height: 10vh;
  border-radius: 10px;
  background-color: rgba(128,138,135,.5);
}
.mu-raised-button-label{
  font-size: 16px;
  color: #f3ebeb;
}
#top{
  top: 42%;
  animation: top0 3s;
  -webkit-animation: top0 3s;
}
#bottom{
  top: 60%;
}
#left{
  top: 42%;
}
#right{
  top: 25%;
  right: 20%;
}

.mu-ripple-wrapper{
  display: flex;
  flex-direction: column;
}
h2{
  padding:10px 0;
}
</style>



